.resume {
  max-width: 100rem;
  width: 90%;
  flex-grow: 1;
  display: flex;
  display: -webkit-flex;
  /* Safari */
  border-radius: 0.5rem;
  position: relative;
  z-index: 1;
}
.resume .pdf-loading {
  position: absolute;
  left: 50%;
  top: 8rem;
  transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%);
  -o-transform: translate(-50%, -50%);
  color: #fff;
  font-size: 2rem;
  text-align: center;
}
.resume .resume-cont {
  position: relative;
  z-index: 2;
  width: 70rem;
  min-height: 300rem;
  margin: 0 auto;
}
.resume object {
  width: 100%;
  height: 100%;
}
.resume .no-support-pdf {
  text-align: left;
  font-size: 1.4rem;
  background: #fff;
  border-radius: 0.5rem;
  padding: 1rem;
  text-indent: 2.8rem;
  line-height: 2.6rem;
}
