.about {
  display: flex;
  display: -webkit-flex;
  /* Safari */
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  flex-grow: 0.5;
  background: #fff;
  width: 100%;
}
.about .logo {
  width: 8rem;
  height: 8rem;
  border-radius: 50%;
  overflow: hidden;
  background: url(/static/media/home_logo.c2dee6f7.jpg) no-repeat center;
  background-size: cover;
}
.about .logo-logout {
  border: 0.2rem solid #ccc;
}
.about .logo-login {
  border: 0.2rem solid #1890ff;
}
.about .login-form {
  display: flex;
  display: -webkit-flex;
  /* Safari */
  flex-direction: column;
  align-items: flex-end;
  justify-content: space-between;
  height: 9rem;
}
.about .login-form input {
  height: 2rem;
  line-height: 2rem;
}
.about .info {
  display: flex;
  display: -webkit-flex;
  /* Safari */
  flex-direction: column;
  justify-content: space-around;
  min-height: 8rem;
  width: 30%;
  max-width: 119rem;
  text-align: center;
}
.about .info > p {
  min-width: 50%;
  color: #333;
  font-weight: bold;
  font-size: 1.4rem;
  white-space: nowrap;
}
.about button {
  height: 2rem;
  line-height: 2rem;
  min-width: 4rem;
}
