.blog {
  /*.flex();
    justify-content: center;
    flex-grow:1;
    align-self: center;
    border-radius: .5rem;*/
  max-width: 100rem;
  width: 90%;
  flex-grow: 1;
  display: flex;
  display: -webkit-flex;
  /* Safari */
}
.blog .list {
  /*.flex();
        justify-content: space-around;
        align-content: space-around;
        flex-wrap: wrap;
        padding-top: 4rem;
        background: #fff;
        border-radius: .5rem;*/
  width: 100%;
  display: flex;
  flex-direction: column;
}
.blog .list .add-blog {
  text-align: right;
}
.blog .list .add-blog a {
  display: inline-block;
  background: #fff;
  color: #333;
  padding: 0.8rem 1.6rem;
  border-radius: 0.5rem;
  margin-bottom: 2rem;
  font-size: 1.8rem;
  cursor: pointer;
  text-decoration: none;
}
.blog .list .article-item {
  border-radius: 0.5rem;
  border: 0.1rem solid #ccc;
  padding: 2rem;
  margin-bottom: 2rem;
  background: #ffffff;
}
.blog .list .article-item a {
  display: flex;
  display: -webkit-flex;
  /* Safari */
  flex-direction: column;
  height: 10rem;
  overflow: hidden;
  color: #333;
  text-decoration: none;
}
.blog .list .article-item .title {
  font-size: 1.8rem;
}
.blog .list .article-item .message {
  padding: 1rem 0;
}
.blog .list .article-item .content {
  padding: 0;
  margin: 0;
}
.blog .detail {
  border-radius: 0.5rem;
  background: #ffffff;
  padding: 2rem;
  width: 100%;
  box-sizing: border-box;
}
.blog .detail .title {
  padding-bottom: 1rem;
  font-size: 2.2rem;
}
.blog .detail .btn-operate {
  text-align: right;
}
.blog .detail .btn-operate button {
  display: inline-block;
  color: #fff;
  outline: none;
  border: none;
  height: 2rem;
  line-height: 2rem;
  border-radius: 0.4rem;
  width: 4rem;
  cursor: pointer;
  font-size: 1.2rem;
  text-align: center;
  text-decoration: none;
  margin-left: 1rem;
}
.blog .detail .btn-operate .btn-delete {
  background-color: #f00;
}
.blog .detail .btn-operate .btn-edit {
  background-color: #1890ff;
}
.blog .detail .content {
  padding-top: 1rem;
  line-height: 2rem;
  word-wrap: break-word;
}
.blog .detail .content pre {
  padding: 1rem;
  margin: 0.8rem 0;
  background-color: #f1f2f3;
  border-radius: 3px;
  color: #666;
}
.blog .edit {
  /*        .flex();
        justify-content: center;
        flex-grow:1;
        background: #fff;
        max-width: 90%;
        align-self: center;*/
  padding: 1rem 2rem 0;
  background: #ffffff;
  border-radius: 0.5rem;
  width: 100%;
}
.blog .edit .edit-content {
  display: flex;
  display: -webkit-flex;
  /* Safari */
  flex-direction: column;
  background: #fff;
  color: #333;
  padding-bottom: 5rem;
}
.blog .edit .edit-content .edit-item {
  display: flex;
  display: -webkit-flex;
  /* Safari */
  flex-direction: column;
  padding-top: 3rem;
}
.blog .edit .edit-content .edit-item > p {
  text-align: left;
  margin-bottom: 1rem;
  font-weight: bold;
}
.blog .edit .edit-content .item-submit {
  align-items: center;
}
.blog .edit .edit-content .title {
  height: 2.8rem;
  line-height: 2.8rem;
}
.blog .edit .edit-content .article-braft-edit {
  border: 0.1rem solid #ccc;
  border-radius: 0.5rem;
}
.blog .edit .edit-content .article-braft-edit .bf-content {
  /* 滚动槽 */
  /* 滚动条滑块 */
}
.blog .edit .edit-content .article-braft-edit .bf-content::-webkit-scrollbar {
  width: 6px;
}
.blog .edit .edit-content .article-braft-edit .bf-content::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(20, 20, 20, 0.3);
  border-radius: 10px;
}
.blog .edit .edit-content .article-braft-edit .bf-content::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background: rgba(20, 20, 20, 0.1);
  -webkit-box-shadow: inset 0 0 6px rgba(20, 20, 20, 0.5);
}
.blog .edit .edit-content .submit {
  color: #fff;
  outline: none;
  border: none;
  background-color: #1890ff;
  height: 3.2rem;
  line-height: 3.2rem;
  border-radius: 0.4rem;
  width: 30%;
  cursor: pointer;
  font-size: 1.6rem;
  min-width: 11rem;
}
