.header {
  width: 60%;
  max-width: 119rem;
  margin: 0 auto;
  height: 15rem;
  text-align: center;
  display: flex;
  display: -webkit-flex;
  /* Safari */
  justify-content: center;
}
.header ul {
  padding: 1rem 0;
  display: flex;
  display: -webkit-flex;
  /* Safari */
  justify-content: space-around;
  flex-wrap: wrap;
  width: 100%;
  align-content: space-around;
}
.header ul li {
  margin: 0 1rem;
  text-align: center;
  display: flex;
  display: -webkit-flex;
  /* Safari */
  align-items: center;
}
.header ul li a {
  padding: 0.5rem 1rem;
  font-size: 1.6rem;
  display: inline-block;
  color: white;
  text-decoration: none;
  cursor: pointer;
}
